body {
  margin: 0;
	font-family: "NanumSquare";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#menu-button {
	z-index: 4;
	position: fixed;
	width: 50px;
	height: 50px;
	right: 30px;
	top: 30px;
	color: #ffffff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


header {
	width: 100%;
	height: 50px;
	z-index: 10;
	top: 0px;
	position: fixed;
	background-color: #000000;
	border-bottom: 1px solid rgba(150, 150, 150, 0.7);
	box-sizing: border-box;
	transition: All 0.2s ease;
	-webkit-transition: All 0.2s ease;
	-moz-transition: All 0.2s ease;
	-o-transition: All 0.2s ease;
	text-align: center;
	font-family: "NanumSquare";
	color: white;
	line-height: 50px;
	opacity: 0.7;
}

#header {
	color: white;
	font-family: "NanumSquare";
	font-size: 20px;
}

#selector {
	color: black;
	font-family: "NanumSquare";
	font-size: 20px;
}

#sidebar_open {
	position: fixed;
	overflow: hidden;
}

.home_container {
	height: 2400px;
}

#home_grid {
	margin-left: 100px;
	margin-right: 100px;
}

#home_card {
	margin: 0 auto;
}

@media (min-width: 769px) {
	#box {
		margin: 0 auto;
		margin-top: 50px;
		width: 50%;
	}
	.center.square {
		margin: auto;
		width: 60%;
	}
	.center.square.big {
		margin: auto;
		width: 90%;
	}
	.center.vertical.big {
		margin: auto;
		width: 35%;
	}
	.center.vertical {
		margin: auto;
		width: 45%;
	}
}

@media (max-width: 768px) {
	#box {
		margin-top: 75px;
		width: 100%;
	}
	.center.vertical {
		width: 100%;
	}
}

.image-gallery-slide.center {
	border:2px solid #FFFFFF;
	padding: 2px;
}
.gallery_container {
	height: 2120px;
}
